.modal {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 300ms;

  &__container {
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
  }

  &__content {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap: 10px;
    box-shadow: 0px 10px 15px 0px rgba(76, 51, 32, 0.9);
    padding: 30px;
    position: relative;
    transition: all 300ms;
    opacity: 0;
    transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
  }

  &__title {
    text-align: center;
    font-size: 38px;
    font-weight: 400;
    line-height: 150%;
  }

  &__text {
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;
  }

  &__close {
    position: absolute;
    right: 40px;
    top: 20px;

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: var(--color-brown);
      width: 20px;
      height: 3px;
      top: 8px;
      transition: all 300ms;
    }

    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
    &:hover::before {
      transform: rotate(90deg);
    }
    &:hover::after {
      transform: rotate(0deg);
    }
  }

  &__contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }

  &__phone {
    display: flex;
    align-items: center;
    font-size: 24px;
    color: var(--color-brown);
    transition: color 300ms ease-out;
    gap: 5px;
    &:hover {
      color: var(--color-orange);
      transition: color 300ms ease-out;
    }
  }

  &__link {
    display: block;
  }

  &__image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    filter: sepia(60%);
    transition: filter 300ms;
    &:hover {
      filter: none;
      transition: filter 300ms;
    }
  }
}

.modal-show {
  visibility: visible;
  opacity: 1;
  & .modal__content {
    transition: all 300ms;
    transform: perspective(600px) translate(0px, 0px) rotateX(0deg);
    opacity: 1;
  }
}

@media (max-width: 1000px) {
  .modal {
    &__container {
    }

    &__title {
      font-size: 28px;
    }

    &__text {
      font-size: 16px;
    }

    &__contacts {
    }

    &__phone {
      font-size: 20px;
      & img {
        max-height: 20px;
      }
    }

    &__link {
    }

    &__image {
    }
  }
}

